<template>
	<main>
		<div class="topnav">
			<div class="back" @click="back()"></div>
			<h2 class="top-title"></h2>
			<div class="btn">
				<router-link to="/search" class="btn-search"></router-link>
				<!--<router-link to="/index" class="btn-f" @click="show()"></router-link>-->
				<div class="main-menu" v-clickoutside="handleClose">
					<div class="btn-f" @click="mshow=!mshow"></div>
				</div>
			</div>
		</div>
		<section class="paragraph p1">
			<strong class="p-title"></strong>
			<p>1281081466</p>
			<p>2 </p>
		</section>
		<section class="paragraph">
			<strong class="p-title">VIPVIP</strong>
			<p>VIPVIPVIP</p>
			<p>VIPVIP </p>
			<p>2 </p>
			<p>1.8 </p>
		    <p>2.VIP </p>
		    <p>3.QQ </p>
		    <p>4.APP </p>
		    <p>5.VIP </p>
		    <p>6. </p>
		    <p>7.... </p>
		</section>
		<section class="paragraph">
			<strong class="p-title"></strong>
			<p>1=100 </p>
		</section>
		<section class="paragraph">
			<strong class="p-title"></strong>
			<p></p>
			<p>1. </p>
			<p>2.  </p>

		</section>
	</main>
</template>

<script>
	export default{
		data(){
			return {
				mshow:false
			}
		},
		methods:{
			handleClose(){
				this.mshow=false;
			},
			back(){
				this.$router.push('/index')
			}
		},
		//
		directives:{
			clickoutside:{
	            bind:function(el,binding,vnode){
	                function documentHandler(e){
	                    if(el.contains(e.target)){
	                        return false;
	                    }
	                    if(binding.expression){
	                        binding.value(e)
	                    }
	                }
	                el._vueClickOutside_ = documentHandler;
	                document.addEventListener('click',documentHandler);
	            },
	            unbind:function(el,binding){
	                document.removeEventListener('click',el._vueClickOutside_);
	                delete el._vueClickOutside_;
	            }
	        }

		}
	}
</script>

<style scoped>
.topnav{
		background: white;
		/*overflow: hidden;*/
	}
	.back{
		width:27px;
		height: 27px;
		display: inline-block;
		/*padding: 0.375rem;*/
		box-sizing: border-box;
		background: url(../../assets/images/icons_1.png);
		background-position: -80px -251px;
		background-size:300px;
		position: relative;
		top: 14px;
		left: 8px;
	}
	.top-title{
		width: 60px;
		font-size: 18px;
		color: #535252;
		line-height: 1.6rem;
		height: 1.5rem;
		display: inline-block;
		background: white;
		position: relative;
		top: 5px;
		left: 10px;
	}
	.paragraph{
		/*width: 100%;*/
		height: auto;
		display: block;
		padding: 0.6rem 1.50rem;
	    color: #535252;
	    line-height: 2;
	    font-size: 13px;
	    position: relative;
	    border-bottom:1px solid #C5C5C5;
	}
	.p1{
		margin-top: 55px;
	}
	.p-title{
		width: 100%;
		height: auto;
		display: block;
		font-size: 16px;
	}
</style>

















